import Vue from 'vue'
import Vuex from 'vuex'
import {socketurl} from '@/utill/baseUrl/baseurl.js'
Vue.use(Vuex)

const store = new Vuex.Store({
	state: {
		//-------------------------------------
		hasLogin: false, //登录状态切换
		openid: null,
		// ----------------------------
		PagesUrl:'',//跳转的页面
		PagesType:"",//跳转页面的类型
		modelIds:'',
		appTheme:'AppWhite',//app主题背景
		modulebg:'ModuleWhite',//各模块的背景色
		textcolor:'Textblack',//字体颜色
		dataStatus:false,//更改logo以及登陆切换，退出登录时，刷新数据
		connectStatus:false,//未连接
		newNoticeStatus:false,//首页入口红点
		socketTask:null,//socket任务
		is_open_socket:false,//是否打开的状态
		SocketState: {},
		SocketStateErr: {},
		socketData:'',
		timer:null,
	},
	mutations: {
		// 初始化
		SOCKET_INIT(state,id){
			console.log(socketurl)
			console.log(id)
			state.socketTask=uni.connectSocket({ //登陆创建
				url: socketurl+id,
				success:(e)=>{
					console.log(e)
					console.log('创建成功')
				},
				fail:(err)=>{
					console.log(err)
					console.log('创建失败')
				}
			});
			// 监听是否打开
			state.socketTask.onOpen((res) => {
				console.log("WebSocket连接正常打开中...！");
				state.is_open_socket = true;
				// 注：只有连接正常打开中 ，才能正常收到消息
				state.socketTask.onMessage((res) => {
					console.log("收到服务器内容：" + res.data);
					state.socketData=res.data







































				});
				state.timer=setInterval(()=>{ //10s发一次心跳
					 state.socketTask.send({
						data: 'head',
						success(e) {
							// console.log("心跳发送成功");
						},
						fail(e) {
							console.log(e)
							console.log('心跳发送失败')
							clearInterval(state.timer)
							state.is_open_socket = false;
							console.log(uni.getStorageSync('doctorId'))
							state.socketTask=uni.connectSocket({ //登陆创建
								url: socketurl+uni.getStorageSync('doctorId'),
								success:(e)=>{
									console.log(e)
									console.log('心跳断后创建成功')
									// state.is_open_socket = true;
								},
								fail:(err)=>{
									console.log(err)
									console.log('心跳断后创建失败')
								}
							});
							console.log(state.socketTask)
						}
					}); 
				},10000)
			},(err)=>{
				console.log(err)
			})
		},
		// 发送消息
		SOCKET_SEND(state, data) {
			console.log("ws发送！");
			console.log(data)
			state.socketTask.send({
				data: data,
				async success(e) {
					console.log("消息发送成功");
				},
			});
		},
		// setSocketState(that, info) {
		// 	that.SocketState = info;
		// },
		// setSocketStateErr(that, info) {
		// 	that.SocketStateErr = info;
		// },
		// 关闭socket
		SOCKET_CLOSE(state, data){
			state.socketTask.close({
				success(e){
					console.log('关闭socekt')
				},fail(err){
					console.log(err)
				}
			})
		},
		hasLogin(state, provider) {
			console.log(provider)
			state.hasLogin = provider;
		},
		// 保存跳转的页面
		savePages(state,payload){
			console.log(state)
			console.log(payload)
			state.PagesUrl=payload.pages//跳转的页面路径
			state.PagesType=payload.pagetype//跳转方式，1 为switchTab；2为navigateTo
		},
		saveModelIds(state,payload){
			state.modelIds= payload
			console.log(state.modelIds)
		},
		// app主题颜色调整
		changeTheme(state,data){
			console.log(state)
			console.log(data)
			state.appTheme=data.appbg
			state.modulebg=data.moduleBg
			state.textcolor=data.Textcolor
		},
		// 更改全局的数据(即全局页面刷新)
		changeDataStatus(state,data){
			state.dataStatus=data
		},
		connectData(state,data){
			console.log(data)
			state.newNoticeStatus=data
		}
	},
    getters:{
        appTheme(state){
            return state.appTheme
        },
		moduleBg(state){
			return state.modulebg
		},
		textcolor(state){
			return state.textcolor
		},
		DataStatus(state){
			return state.dataStatus
		},
		connect(state){
			console.log(state.newNoticeStatus)
			return state.newNoticeStatus
		}
    },
	actions: {
		// 初始化
		WEBSOCKET_INIT(
			{commit}, id) {
			commit('SOCKET_INIT', id)
		},
		// 发送消息
		WEBSOCKET_SEND({
			commit
		}, data) {
			commit('SOCKET_SEND', data)
		},
		// lazy loading openid
		getUserOpenId: async function ({
			commit,
			state
		}) {
			return await new Promise((resolve, reject) => {
				if (state.openid) {
					resolve(state.openid)
				} else {
					uni.login({
						success: (data) => {
							commit('login')
							setTimeout(function () { //模拟异步请求服务器获取 openid
								const openid = '123456789'
								console.log('uni.request mock openid[' + openid + ']');
								commit('setOpenid', openid)
								resolve(openid)
							}, 1000)
						},
						fail: (err) => {
							console.log('uni.login 接口调用失败，将无法正常使用开放接口等服务', err)
							reject(err)
						}
					})
				}
			})
		}
	}
})
export default store
